"use client";

import { useState, useEffect } from "react";
import Navbar from "@/components/Navbar";
import { format } from "date-fns";
import { ru } from "date-fns/locale";

interface SessionWithTable {
  id: string;
  tableId: number;
  startedAt: string;
  endedAt: string | null;
  duration: number | null;
  actualDuration: number | null;
  hourlyRate: number;
  totalCost: number | null;
  status: "ACTIVE" | "COMPLETED" | "EXPIRED";
  table: {
    id: number;
    name: string;
    hourlyRate: number;
  };
}

// Форматирование цены
function formatPrice(amount: number | null): string {
  if (amount === null) return "—";
  return amount.toLocaleString("ru-RU") + " сум";
}

export default function HistoryPage() {
  const [sessions, setSessions] = useState<SessionWithTable[]>([]);
  const [loading, setLoading] = useState(true);
  const [filter, setFilter] = useState<"all" | "COMPLETED" | "EXPIRED">("all");
  const [totalRevenue, setTotalRevenue] = useState(0);

  useEffect(() => {
    const fetchSessions = async () => {
      setLoading(true);
      try {
        const params = new URLSearchParams({ limit: "100" });
        if (filter !== "all") {
          params.set("status", filter);
        }
        const response = await fetch(`/api/sessions?${params}`);
        if (response.ok) {
          const data = await response.json();
          setSessions(data.sessions);
          // Считаем общую выручку
          const revenue = data.sessions.reduce(
            (sum: number, s: SessionWithTable) => sum + (s.totalCost || 0),
            0
          );
          setTotalRevenue(revenue);
        }
      } catch (error) {
        console.error("Error fetching sessions:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchSessions();
  }, [filter]);

  const formatDuration = (minutes: number | null) => {
    if (minutes === null) return "—";
    const hours = Math.floor(minutes / 60);
    const mins = minutes % 60;
    if (hours > 0) {
      return `${hours} ч ${mins} мин`;
    }
    return `${mins} мин`;
  };

  const getStatusBadge = (status: string) => {
    switch (status) {
      case "ACTIVE":
        return (
          <span className="px-2 py-1 text-xs font-medium rounded-full bg-green-500/20 text-green-400">
            Активна
          </span>
        );
      case "COMPLETED":
        return (
          <span className="px-2 py-1 text-xs font-medium rounded-full bg-blue-500/20 text-blue-400">
            Завершена
          </span>
        );
      case "EXPIRED":
        return (
          <span className="px-2 py-1 text-xs font-medium rounded-full bg-yellow-500/20 text-yellow-400">
            Истекла
          </span>
        );
      default:
        return null;
    }
  };

  return (
    <div className="min-h-screen bg-gray-900">
      <Navbar />

      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="flex flex-col sm:flex-row items-start sm:items-center justify-between gap-4 mb-6">
          <div>
            <h1 className="text-2xl font-bold text-white">История сессий</h1>
            {totalRevenue > 0 && (
              <p className="text-yellow-400 mt-1">
                Общая выручка: {formatPrice(totalRevenue)}
              </p>
            )}
          </div>

          {/* Фильтр */}
          <div className="flex gap-2">
            {[
              { value: "all", label: "Все" },
              { value: "COMPLETED", label: "Завершённые" },
              { value: "EXPIRED", label: "Истекшие" },
            ].map((option) => (
              <button
                key={option.value}
                onClick={() => setFilter(option.value as typeof filter)}
                className={`px-4 py-2 rounded-lg text-sm font-medium transition ${
                  filter === option.value
                    ? "bg-green-600 text-white"
                    : "bg-gray-800 text-gray-300 hover:bg-gray-700"
                }`}
              >
                {option.label}
              </button>
            ))}
          </div>
        </div>

        {loading ? (
          <div className="flex items-center justify-center py-20">
            <svg className="animate-spin h-10 w-10 text-green-500" viewBox="0 0 24 24">
              <circle
                className="opacity-25"
                cx="12"
                cy="12"
                r="10"
                stroke="currentColor"
                strokeWidth="4"
                fill="none"
              />
              <path
                className="opacity-75"
                fill="currentColor"
                d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"
              />
            </svg>
          </div>
        ) : sessions.length === 0 ? (
          <div className="text-center py-20">
            <div className="text-gray-400 text-lg">История пуста</div>
          </div>
        ) : (
          <div className="bg-gray-800 rounded-xl overflow-hidden overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="border-b border-gray-700">
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">
                    Стол
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">
                    Начало
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">
                    Конец
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">
                    Длительность
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">
                    Стоимость
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-gray-400">
                    Статус
                  </th>
                </tr>
              </thead>
              <tbody>
                {sessions.map((session) => (
                  <tr
                    key={session.id}
                    className="border-b border-gray-700/50 hover:bg-gray-700/30"
                  >
                    <td className="px-6 py-4 text-white font-medium">
                      {session.table.name}
                    </td>
                    <td className="px-6 py-4 text-gray-300">
                      {format(new Date(session.startedAt), "dd MMM yyyy, HH:mm", {
                        locale: ru,
                      })}
                    </td>
                    <td className="px-6 py-4 text-gray-300">
                      {session.endedAt
                        ? format(new Date(session.endedAt), "HH:mm", {
                            locale: ru,
                          })
                        : "—"}
                    </td>
                    <td className="px-6 py-4 text-gray-300">
                      {formatDuration(session.actualDuration)}
                    </td>
                    <td className="px-6 py-4 text-yellow-400 font-medium">
                      {formatPrice(session.totalCost)}
                    </td>
                    <td className="px-6 py-4">
                      {getStatusBadge(session.status)}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </main>
    </div>
  );
}
